$theme-color: #0051ad;
$btn-color:#ff7200;
$common-color: #3a3a3a;
$fz-12: 12px;
$main-content-w: 1200px;

.mt13{
    margin-top: 13px;
}
.mt60{
    margin-top: 60px;
}
.ml50{
    margin-left: 50px;
}
.ml16{
    margin-left: 16px;
}
.main-content-w{
    width: $main-content-w;
    margin: 0 auto;
}
.ftheme-color{
    color: $theme-color;
}
.fbtn-color{
    color: $btn-color !important;
}
.bg-theme-color{
    background-color: $theme-color !important;
}
.float-l{
    float: left;
}
.float-r{
    float: right;
}
.clearfix{ clear:both}
.clearfix:after{
    display:block;
    content:".";
    clear:both;
    font-size: 0;
    height: 0;
    visibility: hidden;
}


ul,
ol,
li,
dl,
dt,
dd,
label{
    margin: 0;
    padding: 0;
    list-style: none;
}

a{
    outline:none;
    margin: 0;
    padding:0;
}
a:link{
    text-decoration:none;
}
a:visited{
    text-decoration:none;
}
a:hover{
    text-decoration:none;
}
a:active{
    text-decoration:none;
}

.cart-empty {
	margin-top: 120px;
	h2 {
		font-size: 35px;
		color: #7a7a7a;
		font-weight: 400;
		line-height: 250px;
		text-align: center;
		i {
			display: inline-block;
			padding: 0 30px;
			font-size: 74px;
			line-height: 250px;
			vertical-align: middle;
			color: #b7b7b7;
		}
	}
	button {
		display: block;
		width: 176px;
		margin: 0 auto;
		font-size: 14px;
		height: 36px;
		line-height: 36px;
		color: #fff;
		background: $btn-color;
		text-align: center;
		border-radius: 3px;
		letter-spacing: 10px;
	}
}



